import { useMutation } from '@apollo/client';
import { SET_ASSET_COLLECTIONS } from '../mutations';
export default function useSetAssetCollections() {
    const [action, { error, data, loading }] = useMutation(SET_ASSET_COLLECTIONS);
    const setAssetCollections = ({ asset, assetCollections }) => action({
        variables: {
            id: asset.id,
            assetSourceId: asset.assetSource.id,
            assetCollectionIds: assetCollections.map((c) => c.id),
        },
        optimisticResponse: {
            __typename: 'Mutation',
            setAssetCollections: Object.assign(Object.assign({}, asset), { collections: assetCollections }),
        },
    });
    return { setAssetCollections: setAssetCollections, data, error, loading };
}
