import { useMutation } from '@apollo/client';
import { SET_ASSET_TAGS } from '../mutations';
export default function useSetAssetTags() {
    const [action, { error, data, loading }] = useMutation(SET_ASSET_TAGS);
    const setAssetTags = ({ asset, tags }) => action({
        variables: {
            id: asset.id,
            assetSourceId: asset.assetSource.id,
            tagIds: tags.map((tag) => tag.id),
        },
        optimisticResponse: {
            __typename: 'Mutation',
            setAssetTags: Object.assign(Object.assign({}, asset), { tags }),
        },
    });
    return { setAssetTags, data, error, loading };
}
