import { useMutation } from '@apollo/client';
import { UPDATE_TAG } from '../mutations';
export default function useUpdateTag() {
    const [action, { error, data, loading }] = useMutation(UPDATE_TAG);
    const updateTag = ({ tag, label }) => action({
        variables: {
            id: tag.id,
            label,
        },
        optimisticResponse: {
            updateTag: Object.assign(Object.assign(Object.assign({}, tag), { label }), (label
                ? {
                    label,
                }
                : {})),
        },
    });
    return { updateTag, data, error, loading };
}
