import { useMutation } from '@apollo/client';
import { UPDATE_ASSET } from '../mutations';
export default function useUpdateAsset() {
    const [action, { error, data, loading }] = useMutation(UPDATE_ASSET);
    const updateAsset = ({ asset, label, caption, copyrightNotice }) => action({
        variables: {
            id: asset.id,
            assetSourceId: asset.assetSource.id,
            label,
            caption,
            copyrightNotice,
        },
        optimisticResponse: {
            __typename: 'Mutation',
            updateAsset: Object.assign(Object.assign({}, asset), { label,
                caption,
                copyrightNotice }),
        },
    });
    return { updateAsset, data, error, loading };
}
