import { useMutation } from '@apollo/client';
import { ASSET_COLLECTIONS } from '../queries';
import { DELETE_ASSET_COLLECTION } from '../mutations';
export default function useDeleteAssetCollection() {
    const [action, { error, data, loading }] = useMutation(DELETE_ASSET_COLLECTION);
    const deleteAssetCollection = (id) => action({
        variables: {
            id,
        },
        optimisticResponse: {
            deleteAssetCollection: {
                success: true,
                __typename: 'DeleteAssetCollectionResult',
            },
        },
        update(cache) {
            const { assetCollections } = cache.readQuery({ query: ASSET_COLLECTIONS });
            cache.writeQuery({
                query: ASSET_COLLECTIONS,
                data: { assetCollections: assetCollections.filter((c) => c.id !== id) },
            });
        },
    });
    return { deleteAssetCollection, data, error, loading };
}
