import * as React from 'react';
import { createContext, useContext } from 'react';
export const NotifyContext = createContext(null);
export const useNotify = () => useContext(NotifyContext);
export function NotifyProvider({ children, notificationApi }) {
    const error = (title, message = '') => notificationApi['error'](title, message);
    const warning = (title, message = '') => notificationApi['warning'](title, message);
    const ok = (title) => notificationApi['ok'](title);
    const info = (title) => notificationApi['info'](title);
    const notice = (title) => notificationApi['notice'](title);
    return React.createElement(NotifyContext.Provider, { value: { notice, error, ok, info, warning } }, children);
}
